import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  TouchableOpacity,
  Image,
  Alert,
  ScrollView,
  FlatList,
  Modal,
  TouchableHighlight,
} from 'react-native';
import axios from 'axios'
import {NavigationEvents} from 'react-navigation'
import Ionicons from '@expo/vector-icons/Ionicons'




export default class TaskCompleteScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {
          data: [],
          modalVisible:false,
          userSelected:[],
        };
      }
      componentDidMount(){
        try{
        axios.get("http://124.30.96.18:2000/completetask")
        .then((response) =>{
          this.setState({data: response.data});
          console.log(this.state.data)
        })
      }
      catch(error)
      {
        alert('Connection to server lost')
      }
      }  
      clickEventListener = (item) => {
        this.setState({userSelected: item}, () =>{
          this.setModalVisible(true);
        });
      }
      setModalVisible(visible) {
        this.setState({modalVisible: visible});
      }

      close = (item) => {
        Alert.alert(item.taskname,"Task closed successfully")
      }
    renderItem = ({item}) => {
        return (
          <TouchableOpacity onPress={() => {this.clickEventListener(item)}}>
            <View style={styles.row}>
              <Image source={{ uri: item.image }} style={styles.pic} />
              <View>
                <View style={styles.nameContainer}>
                  <Text style={styles.nameTxt} numberOfLines={1} ellipsizeMode="tail">{item.fromuser}</Text>
                  <TouchableHighlight onPress={() => this.close(item)}>
                    <Ionicons style={styles.icon} name="ios-done-all" size={40}/>
                  </TouchableHighlight>
                </View>
                <View style={styles.nameContainer}>
                <Text style={styles.nameTxt} numberOfLines={1} ellipsizeMode="tail">{item.taskname}</Text>
               </View>               
              </View>
            </View>
          </TouchableOpacity>
          
        );
      }   
    render(){
        return(
            <View style={{ flex: 1 }} >
              <NavigationEvents onDidFocus={()=>
              axios.get("http://124.30.96.18:2000/completetask")
              .then((response) =>{
                this.setState({data: response.data});
                console.log(this.state.data)
              })
            }/>
            <FlatList 
              extraData={this.state}
              data={this.state.data}
              keyExtractor = {(item) => {
                return item.taskid.toString();
              }}
              ListEmptyComponent={
                <View style = {{ marginTop:50, alignItems: 'center' }}>
                  <Text style={{ fontWeight:'bold'}}>
                    No Completed tasks
                  </Text>
                  </View>
              }
              renderItem={this.renderItem}/>
              <Modal
          animationType={'fade'}
          transparent={true}
          onRequestClose={() => this.setModalVisible(false)}
          visible={this.state.modalVisible}>

          <View style={styles.popupOverlay}>
            <View style={styles.popup}>
              <View style={styles.popupContent}>
                <ScrollView contentContainerStyle={styles.modalInfo}>
                    <Image style={styles.image} source={{uri: this.state.userSelected.image}}/>
                    <Text style={styles.name}>{this.state.userSelected.fromuser}</Text>
                    <Text style={styles.position}>{this.state.userSelected.designation}</Text>
                    <Text style={styles.about}>{this.state.userSelected.taskname}</Text>
                    <Text style={styles.abouttext}>{this.state.userSelected.taskinfo}</Text>
                </ScrollView>
              </View>
              <View style={styles.popupButtons}>
                <TouchableOpacity onPress={() => {this.setModalVisible(false) }} style={styles.btnClose}>
                  <Text style={styles.txtClose}>Close</Text>
                </TouchableOpacity>
              </View>
            </View>
          </View>
        </Modal>
        </View>
        );
    }
}

const styles= StyleSheet.create({
    container:{
        flex:1,
        alignItems:'center',
        justifyContent: 'center'
    },
    row: {
        flexDirection: 'row',
        alignItems: 'center',
        borderColor: '#DCDCDC',
        backgroundColor: '#fff',
        borderBottomWidth: 1,
        padding: 10,
      },
      pic: {
        borderRadius: 30,
        width: 60,
        height: 60,
      },
      nameContainer: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        width: 280,
      },
      nameTxt: {
        marginLeft: 15,
        fontWeight: '600',
        color: '#222',
        fontSize: 18,
        width:170,
      },
      mblTxt: {
        fontWeight: '200',
        color: '#777',
        fontSize: 13,
      },
      msgContainer: {
        flexDirection: 'row',
        alignItems: 'center',
      },
      msgTxt: {
        fontWeight: '400',
        color: '#008B8B',
        fontSize: 20,
        marginLeft: 15,
      },
      /************ modals ************/
  popup: {
    backgroundColor: 'white',
    marginTop: 80,
    marginHorizontal: 20,
    borderRadius: 7,
  },
  popupOverlay: {
    backgroundColor: "#00000057",
    flex: 1,
    marginTop: 30
  },
  popupContent: {
    //alignItems: 'center',
    margin: 5,
    height:250,
  },
  popupHeader: {
    marginBottom: 45
  },
  popupButtons: {
    marginTop: 15,
    flexDirection: 'row',
    borderTopWidth: 1,
    borderColor: "#eee",
    justifyContent:'center'
  },
  popupButton: {
    flex: 1,
    marginVertical: 16
  },
  btnClose:{
    height:20,
    backgroundColor:'#20b2aa',
    padding:20
  },
  modalInfo:{
    alignItems:'center',
    justifyContent:'center',
  },
  name:{
    fontSize:18,
    flex:1,
    alignSelf:'center',
    color:"#008080",
    fontWeight:'bold'
  },
  position:{
    fontSize:16,
    flex:1,
    alignSelf:'center',
    color:"#008080"
  },
  about:{
    marginHorizontal:10,
    color:"#FF0000"
  },
  abouttext:{
    marginHorizontal:10,
    color:"#000000"
  },
  image:{
    width:90,
    height:90,
    borderRadius:45,
  },
});