import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  TouchableHighlight,
  Image,
  Alert,
  ScrollView,
  TextInput,
  FlatList,
  BackHandler
} from 'react-native';
import axios from 'axios'
import { TouchableOpacity } from 'react-native-gesture-handler';
import CustomActionButton from '../components/CustomActionButton'
import colors from '../assets/colors'
import Ionicons from '@expo/vector-icons/Ionicons'
const serverUrl = 'http://124.30.96.18:2000'
const http = axios.create({
    baseURL: serverUrl, 
});



export default class Userwisetask extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      data: [],
      contacts:[]
    };
  }
 async componentDidMount(){
    try{
    axios.get("http://124.30.96.18:2000/myteamusers")
    .then((response) =>{
      this.setState({data: response.data});
      this.setState({contacts: response.data});
      console.log(this.state.data)
    })
    BackHandler.addEventListener("hardwareBackPress", this.backAction);
  }
  catch(error)
  {
    alert(error)
  }
  }
  componentWillUnmount() {
    BackHandler.removeEventListener("hardwareBackPress", this.backAction);
  }

  backAction = () => {
    Alert.alert("Hold on!", "Are you sure you want to go back?", [
      {
        text: "Cancel",
        onPress: () => null,
        style: "cancel"
      },
      { text: "YES", onPress: () => this.goback() }
    ]);
    return true;
  };

  goback = () => {
    this.props.navigation.navigate('Department')
  }
  clickEventListener(item) {
    this.props.navigation.navigate('SpecificUser',{username:item.id})
}
  searchContacts = (value) => {
    const filteredContacts = this.state.contacts.filter(
      contact => {
        let contactLowercase = (contact.username).toLowerCase();
        let searchTermLowercase = value.toLowerCase();
        return contactLowercase.indexOf(searchTermLowercase) > -1;
      });
      this.setState({ data: filteredContacts});
  }

  render() {
    return (
      <View style={styles.container}>
        <View style={styles.formContent}>
          <View style={styles.inputContainer}>
            <Image style={[styles.icon, styles.inputIcon]} source={{uri: 'https://png.icons8.com/search/androidL/100/000000'}}/>
            <TextInput style={styles.inputs}
                ref={'txtPassword'}
                placeholder="Search"
                underlineColorAndroid='transparent'
                onChangeText={(value) => this.searchContacts(value)}/>
          </View>
        </View>

        <FlatList 
          style={styles.notificationList}
          data={this.state.data}
          keyExtractor= {(item) => {
            return item.id.toString();
          }}
          ListEmptyComponent={
            <View style = {{ marginTop:50, alignItems: 'center' }}>
              <Text style={{ fontWeight:'bold'}}>
                You dont have a team
              </Text>
              </View>
          }
          renderItem={({item}) => {
            return (
                <TouchableOpacity onPress={() => {this.clickEventListener(item)}}>
              <View style={styles.notificationBox}>
                <Image style={styles.image}
                  source={{uri: item.image}}/>  
                <Text style={styles.name}>{item.username}</Text>
              </View>
              </TouchableOpacity>
            )}}/>
            <CustomActionButton
        position="right" 
        style={{ backgroundColor: colors.bgPrimary,borderRadius:25 }}
        onPress={this.goback}>
       <Ionicons style={styles.icon} name="ios-undo" size={24}/>
        </CustomActionButton>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#EBEBEB',
    marginBottom:50
  },
  formContent:{
    flexDirection: 'row',
    marginTop:30,
  },
  inputContainer: {
      borderBottomColor: '#F5FCFF',
      backgroundColor: '#FFFFFF',
      borderRadius:30,
      borderBottomWidth: 1,
      height:45,
      flexDirection: 'row',
      alignItems:'center',
      flex:1,
      margin:10,
  },
  icon:{
    width:30,
    height:30,
  },
  iconBtnSearch:{
    alignSelf:'center'
  },
  inputs:{
      height:45,
      marginLeft:16,
      borderBottomColor: '#FFFFFF',
      flex:1,
  },
  inputIcon:{
    marginLeft:15,
    justifyContent: 'center'
  },
  notificationList:{
    marginTop:20,
    padding:10,
  },
  notificationBox: {
    paddingTop:10,
    paddingBottom:10,
    marginTop:5,
    backgroundColor: '#FFFFFF',
    flexDirection: 'row',
    borderRadius:10,
  },
  image:{
    width:45,
    height:45,
    borderRadius:20,
    marginLeft:20
  },
  name:{
    fontSize:20,
    fontWeight: 'bold',
    color: "#000000",
    marginLeft:10,
    alignSelf: 'center'
  },
}); 