import React from 'react';
import { StyleSheet, Text, View,SafeAreaView,TouchableOpacity,TextInput,FlatList,Modal,Dimensions,ScrollView,Image,ActivityIndicator,TouchableHighlight } from 'react-native';
import TaskCount from '../components/TaskCount'
import { Ionicons } from '@expo/vector-icons'
import CustomActionButton from '../components/CustomActionButton'
import colors from '../assets/colors'
import axios from 'axios'
import { ConfirmDialog } from 'react-native-simple-dialogs'
import * as firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'
import {NavigationEvents} from 'react-navigation'
import {Permissions,Notifications} from 'expo';

const serverUrl = 'http://124.30.96.18:2000'
const http = axios.create({
    baseURL: serverUrl, 
});
export default class HomeScreen extends React.Component {
  constructor() {
    super();
    this.state={
      dialogVisible:false,
      totalcount: 0,
      completecount: 0,
      incompletecount: 0,
      isaddnewtaskvisible:false,
      textinputdata:'',
      tasks:[],
      modalVisible:false,
      userSelected:[],
      completeitem:[],
      data:[],
      isLoading:false,
    };
  };


  async componentDidMount(){
    await this.registerForPushNotificationsAsync();
    try{
    this.setState({isLoading: true});
    currentUser = await firebase.auth().currentUser
    var email = currentUser.email
    axios.get("http://124.30.96.18:2000/currentuser")
    .then((response) => {
      if(response.data == "session expired")
      {
        http.post('/login',{email})
        .then((response) => {
          if(response.data == "login success")
          {
            axios.get("http://124.30.96.18:2000/task")
    .then((response) =>{
      this.setState({data: response.data});
      console.log(this.state.data)
      this.setState({isLoading: false});
    })
          } 
        })
      }
    })
    axios.get("http://124.30.96.18:2000/task")
    .then((response) =>{
      this.setState({data: response.data});
      console.log(this.state.data)
      this.setState({isLoading: false});
    })
  }
  catch(error)
  {
    alert('server loading')
  }
  }
  
  registerForPushNotificationsAsync = async() => {
    let token = await Notifications.getExpoPushTokenAsync();
    currentUser = await firebase.auth().currentUser
    var email = currentUser.email
    http.post('/updatetoken',{email,token})
  }

  clickEventListener = (item) => {
    this.setState({userSelected: item}, () =>{
      this.setModalVisible(true);
    });
  }

  completetask = (item) => {
    this.setState({dialogVisible: true})
    this.setState({completeitem: item})
  }

  finish = () => {
    var taskid = this.state.completeitem.taskid
    try{
    http.post('/finish',{taskid})
    .then(() =>{
      axios.get("http://124.30.96.18:2000/task")
      .then((response) =>{
        this.setState({data: response.data});
        console.log(this.state.data)
      })
    }) 
    alert('Task completed successfully')
    this.setState({dialogVisible: false})
    }
    catch(error)
    {
      alert('Connection to server lost')
    }
  };

  setModalVisible(visible) {
    this.setState({modalVisible: visible});
  }

  showAddNewTask = () => {
    this.props.navigation.navigate('AssignedTask');
  };
  hideAddNewTask = () => {
    this.setState({isaddnewtaskvisible:false});
  };
  
  
  addTask = task => {
    this.setState((state,props)=> ({
      tasks: [...state.tasks, task],
      totalcount: state.totalcount + 1,
      incompletecount: state.incompletecount + 1
    }),() => {
      console.log(this.state.tasks)
    });
  };

  markAsRead = (selectedTask,index) => {
  let newList = this.state.tasks.filter(task => task !== selectedTask)
  this.setState(prevState => ({
      tasks : newList,
      completecount: prevState.completecount +1,
      incompletecount: prevState.incompletecount -1
  }));
};


renderItem = (item, index) => (
<View style={{ height: 50, flexDirection:'row' }}>
<View style={{flex:1,justifyContent:'center',paddingLeft:5}}>
<Text>{item}</Text>
</View>
<CustomActionButton style={{width:100,backgroundColor:'#a5deba'}} onPress={()=>this.markAsRead(item,index)}>
<Text style={{fontWeight:'bold',color:'white'}}>Finish</Text>
</CustomActionButton>
</View>
);

  render(){
  return (
    <View style={{flex: 1}}>
      <NavigationEvents onDidFocus={()=>
              axios.get("http://124.30.96.18:2000/task")
              .then((response) =>{
                this.setState({data: response.data});
                console.log(this.state.data)
                this.setState({isLoading: false});
                this.registerForPushNotificationsAsync();
              })
            }/>
      <SafeAreaView/>
      <View style={{flex: 1}}>
        {this.state.isaddnewtaskvisible && (
        <View style={{height: 50, flexDirection:'row'}}>
         <TextInput onChangeText={(text)=>this.setState({textinputdata:text})} 
         style={{flex: 1,backgroundColor: colors.bgTextInput,paddingLeft: 5}}
          placeholder="Enter task name"
          placeholderTextColor='grey'
         />
         <CustomActionButton style={{backgroundColor:colors.bgSuccess}}
         onPress={()=>this.addTask(this.state.textinputdata)}
         >
         <Ionicons name="ios-checkmark" color='white' size={40}/>
         </CustomActionButton>
         <CustomActionButton onPress={this.hideAddNewTask}>
         <Ionicons name="ios-close" color='white' size={40}/>
         </CustomActionButton>
        </View> 
        )}
        <FlatList 
          style={styles.userList}
          columnWrapperStyle={styles.listContainer}
          data={this.state.data}
          keyExtractor= {(item) => {
            return item.taskid.toString();
          }}
          ListEmptyComponent={
            <View style = {{ marginTop:50, alignItems: 'center' }}>
              <Text style={{ fontWeight:'bold'}}>
                No Task Allocated For You
              </Text>
              </View>
          }
          renderItem={({item}) => {
          return (
            <TouchableOpacity onPress={() => {this.clickEventListener(item)}}>
            <View style={styles.box}>
              <Image style={styles.image} source={{uri: item.image}} />
              <View style={styles.boxContent}>
                <Text style={styles.title}>{item.taskname}</Text>
                <Text style={styles.name}>{item.fromuser}</Text>
                <Text style={styles.name}>{item.department}</Text>
                <View style={styles.buttons}>
                </View>         
              </View>
                <TouchableHighlight style={[styles.button, styles.profile]} onPress={() => this.completetask(item)}>
                    <Ionicons style={styles.icon} name="ios-done-all" size={24}/>
                </TouchableHighlight>
                </View>      
            </TouchableOpacity>  
          )}}/>
             <ConfirmDialog
    title="Please Confirm"
    message="Do you want to complete this task?"
    visible={this.state.dialogVisible}
    onTouchOutside={() => this.setState({dialogVisible: false})}
    positiveButton={{
        title: "YES",
        onPress: () => this.finish()
    }}
    negativeButton={{
        title: "NO",
        onPress: () => this.setState({dialogVisible: false})
    }}
/>
     {this.state.isLoading?
    <View style={styles.container}>
    <ActivityIndicator size="large" color={colors.logoColor}></ActivityIndicator>
    </View>:<View></View>}
        <Modal
          animationType={'fade'}
          transparent={true}
          onRequestClose={() => this.setModalVisible(false)}
          visible={this.state.modalVisible}>

          <View style={styles.popupOverlay}>
            <View style={styles.popup}>
              <View style={styles.popupContent}>
                <ScrollView contentContainerStyle={styles.modalInfo}>
                    <Image style={styles.image} source={{uri: this.state.userSelected.image}}/>
                    {this.state.userSelected.completetime?<Text style={styles.name}>Complete within:{this.state.userSelected.completetime}</Text>:<Text></Text>}
                    <Text style={styles.about}>{this.state.userSelected.taskname}</Text>
                    <Text style={styles.abouttext}>{this.state.userSelected.taskinfo}</Text>
                </ScrollView>
              </View>
              <View style={styles.popupButtons}>
                <TouchableOpacity onPress={() => {this.setModalVisible(false) }} style={styles.btnClose}>
                  <Text style={styles.txtClose}>Close</Text>
                </TouchableOpacity>
              </View>
            </View>
          </View>
        </Modal>
      </View>
    <SafeAreaView/>
    </View>
    );
}
}

