import React from 'react'
import {View,Text,Image} from 'react-native'
import {Ionicons} from '@expo/vector-icons'
import colors from '../../assets/colors'
import { AntDesign } from '@expo/vector-icons';
import CustomActionButton from '../../components/CustomActionButton'

export default class WelcomeScreen extends React.Component{
    render(){
      return(
          <View style={{flex:1,backgroundColor: colors.bgMain}}>
              <View style ={{flex:1,alignItems:'center',justifyContent:'center'}}>
              <AntDesign name="team" size={150} color={colors.logoColor} />
               <Text style={{fontSize:50,fontWeight:'100',color:'white'}}>Wee Do</Text>
              </View>
              <View style={{flex:1,alignItems:'center'}}>
                <CustomActionButton
                 style={{
                     width:200,
                     backgroundColor: 'transparent',
                     borderWidth: 0.5,
                     borderColor: colors.bgPrimary,
                     marginBottom: 10
                 }}
                 title="Login in" onPress={()=>this.props.navigation.navigate('LoginScreen')}> 
                 <Text style={{ fontWeight:'100',color:'white'}}>Login</Text>
                 </CustomActionButton>
              </View>
          </View>
      )  
    }
}