import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  TouchableOpacity,
  Image,
  Alert,
  ScrollView,
  TextInput,
  FlatList,
  SafeAreaView,
  ActivityIndicator,
} from 'react-native';
import axios from 'axios'
import CustomActionButton from '../components/CustomActionButton'
import colors from '../assets/colors'
import Ionicons from '@expo/vector-icons/Ionicons'
import {NavigationEvents} from 'react-navigation'






export default class Users extends Component {

  constructor(props) {
    super(props);
    this.state = {
      data: [],
      contacts: [],
      isLoading:false
    };
  }
  componentDidMount(){
    try{
    this.setState({isLoading: true});
    axios.get("http://124.30.96.18:2000/userlist")
    .then((response) =>{
      this.setState({data: response.data});
      this.setState({contacts: response.data});
      this.setState({isLoading: false});
      console.log(this.state.data)
    })
  }
  catch(error)
  {
    alert('Connection to server lost')
  }
  }

  clickEventListener(item) {
      this.props.navigation.navigate('TaskPage',{username:item.id})
  }
  goback = () => {
    this.props.navigation.navigate('HomeScreen')
  }
  
  searchContacts = (value) => {
    const filteredContacts = this.state.contacts.filter(
      contact => {
        let contactLowercase = (contact.username).toLowerCase();
        let searchTermLowercase = value.toLowerCase();
        return contactLowercase.indexOf(searchTermLowercase) > -1;
      });
      this.setState({ data: filteredContacts});
  }
  

  render() {
    return (
      <View style={styles.container}>
        <View style={styles.formContent}>
          <View style={styles.inputContainer}>
          <Ionicons name="ios-search" size={30}
         color={colors.logoColor}
         onPress={()=>navigation.openDrawer()}
         style={{ marginLeft: 10}}/>
            <TextInput style={styles.inputs}
              ref={'txtSearch'}
              placeholder="Search"
              placeholderTextColor="#FFF"
              underlineColorAndroid='transparent'
              onChangeText={(value) => this.searchContacts(value)}/>
          </View>
        </View>
        <FlatList style={styles.list}
          contentContainerStyle={styles.listContainer}
          data={this.state.data}
          horizontal={false}
          numColumns={2}
          keyExtractor= {(item) => {
            return item.email.toString();
          }}
          ListEmptyComponent={
            <View style = {{ marginTop:50, alignItems: 'center' }}>
              <Text style={{ fontWeight:'bold'}}>
                You cannot assign task to others
              </Text>
              </View>
          }
          renderItem={({item}) => {
            return (
              <TouchableOpacity style={styles.card} onPress={() => {this.clickEventListener(item)}}>
                <Image style={styles.userImage} source={{uri:item.image}}/>
                <View style={styles.cardFooter}>
                  <View style={{alignItems:"center", justifyContent:"center"}}>
                    <Text style={styles.name}>{item.username}</Text>
                    <Text style={styles.position}>{item.designation}</Text>
                    <TouchableOpacity style={styles.followButton} onPress={()=> this.clickEventListener(item)}>
                      <Text style={styles.followButtonText}>Assign Task</Text>  
                    </TouchableOpacity>
                  </View>
                </View>
              </TouchableOpacity>
            )
          }}/>
           {this.state.isLoading?
    <View style={styles.container}>
    <ActivityIndicator size="large" color={colors.logoColor}></ActivityIndicator>
    </View>:null}
           <CustomActionButton
        position="right" 
        style={{ backgroundColor: colors.bgPrimary,borderRadius:25 }}
        onPress={this.goback}>
       <Ionicons style={styles.icon} name="ios-undo" size={24}/>
        </CustomActionButton>
        <NavigationEvents onDidFocus={()=>
            axios.get("http://124.30.96.18:2000/userlist")
            .then((response) =>{
              this.setState({data: response.data});
              console.log(this.state.data)
            })
          }/>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container:{
    flex:1,
    justifyContent:"center",
    marginBottom:50
  },
  list: {
    paddingHorizontal: 5,
    backgroundColor:"#E6E6E6",
  },
  listContainer:{
   alignItems:'center'
  },
  /******** card **************/
  card:{
    shadowColor: '#00000021',
    shadowOffset: {
      width: 0,
      height: 6,
    },
    shadowOpacity: 0.37,
    shadowRadius: 7.49,
    elevation: 12,

    marginVertical: 5,
    backgroundColor:"white",
    flexBasis: '46%',
    marginHorizontal: 5,
  },
  cardFooter: {
    paddingVertical: 17,
    paddingHorizontal: 16,
    borderTopLeftRadius: 1,
    borderTopRightRadius: 1,
    flexDirection: 'row',
    alignItems:"center", 
    justifyContent:"center"
  },
  cardContent: {
    paddingVertical: 12.5,
    paddingHorizontal: 16,
  },
  cardHeader:{
    flexDirection: 'row',
    justifyContent: 'space-between',
    paddingTop: 12.5,
    paddingBottom: 25,
    paddingHorizontal: 16,
    borderBottomLeftRadius: 1,
    borderBottomRightRadius: 1,
  },
  userImage:{
    height: 120,
    width: 120,
    borderRadius:60,
    alignSelf:'center',
    borderColor:"#DCDCDC",
    borderWidth:3,
  },
  name:{
    fontSize:18,
    flex:1,
    alignSelf:'center',
    color:"#008080",
    fontWeight:'bold'
  },
  position:{
    fontSize:14,
    flex:1,
    alignSelf:'center',
    color:"#696969"
  },
  followButton: {
    marginTop:10,
    height:35,
    width:150,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius:30,
    backgroundColor: "#00BFFF",
  },
  followButtonText:{
    color: "#FFFFFF",
    fontSize:20,
  },
  icon:{
    height: 20,
    width: 20, 
  },
  /*****search bar *******/
  formContent:{
    flexDirection: 'row',
    marginTop:30,
  },
  inputContainer: {
    borderBottomColor: '#F5FCFF',
    backgroundColor: colors.bgMain,
    borderRadius:30,
    borderBottomWidth: 1,
    height:45,
    flexDirection: 'row',
    alignItems:'center',
    flex:1,
    margin:10,
  },
  inputIcon:{
    marginLeft:15,
    justifyContent: 'center'
  },
  inputs:{
    height:45,
    marginLeft:16,
    borderBottomColor: '#FFFFFF',
    flex:1,
    color: '#FFF',
  },
});