import React,{ Component } from 'react';
import {View, Text, StyleSheet,TextInput, Alert } from 'react-native';
import CustomActionButton from '../components/CustomActionButton'
import  colors from '../assets/colors';
import * as firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'
import axios from 'axios'
const serverUrl = 'http://124.30.96.18:2000'
const http = axios.create({
    baseURL: serverUrl, 
});



class SettingsScreen extends React.Component {

  constructor(){
    super()
    this.state={
        currentpassword:'',
        newpassword:'',
        isLoading:false
        }
     }

  signOut = async() => {
    try{
        http.get('/logout')
        await firebase.auth().signOut()
        this.props.navigation.navigate('WelcomeScreen')
    }catch(error)
    {
        alert('Unable to sign out')
    }
  }

  goBack = () => {
    this.props.navigation.navigate('HomeScreen')
  }

  reauthenticate = (currentpassword) => {
    var user = firebase.auth().currentUser;
    var cred = firebase.auth.EmailAuthProvider.credential(user.email,currentpassword);
    return user.reauthenticateWithCredential(cred);
  }

  changePassword = () => {
    try{
      this.reauthenticate(this.state.currentpassword).then(()=>{
        var user = firebase.auth().currentUser;
        user.updatePassword(this.state.newpassword).then(()=>{
        var password = this.state.newpassword
        http.post('/changepassword',{password})
        alert("Password Changed Successfully")
        }).catch((error)=>{
         alert(error.message)    
        });
      }).catch((error) => {
         alert(error.message)
      });
      }
      catch(error){
        console.log(error)
        alert("Password was not updated.Connection to server lost")
      }
  }


    render() {
        return (
          <View style={styles.container}>
            <Text>Reset Password</Text>
             <TextInput style={styles.textInput}
              placeholder="Enter your Old Password"
              placeholderTextColor={colors.bgTextInputDark}
              keyboardType="email-address"
              onChangeText = {currentpassword => this.setState({currentpassword:currentpassword})} >
          </TextInput> 
          <TextInput style={styles.textInput}
              placeholder="Enter your New Password"
              placeholderTextColor={colors.bgTextInputDark}
              keyboardType="email-address"
              onChangeText = {newpassword => this.setState({newpassword:newpassword})} >
          </TextInput>

          <CustomActionButton
                 style={{
                     width:300,
                     backgroundColor: 'transparent',
                     borderWidth: 0.5,
                     borderColor: colors.bgPrimary,
                     marginBottom: 10
                 }}
                 title="Change Password" onPress={this.changePassword}> 
                 <Text style={{ fontWeight:'100',color:colors.bgError}}>Change Password</Text>
                 </CustomActionButton>
           
               <CustomActionButton
                 style={{
                     width:200,
                     backgroundColor: 'transparent',
                     borderWidth: 0.5,
                     borderColor: colors.bgPrimary,
                     marginBottom: 10
                 }}
                 title="Login in" onPress={this.signOut}> 
                 <Text style={{ fontWeight:'100',color:'white'}}>Logout</Text>
                 </CustomActionButton>
                 <CustomActionButton
                 style={{
                     width:200,
                     backgroundColor: 'transparent',
                     borderWidth: 0.5,
                     borderColor: colors.bgPrimary,
                     marginBottom: 10
                 }}
                 title="Login in" onPress={this.goBack}> 
                 <Text style={{ fontWeight:'100',color:'white'}}>Go Back</Text>
                 </CustomActionButton>
          </View>
        );
    }
}
export default SettingsScreen;

const styles = StyleSheet.create({
    container: {
        flex:1,
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor:colors.bgMain
    },
    textInput:{
      height:50,
      width:300,
      borderWidth:1,
      borderColor:colors.borderColor,
      marginHorizontal: 40,
      marginBottom: 10,
      marginTop: 10,
      color:colors.txtWhite,
      paddingHorizontal: 10
  },
});