import React, { Component } from "react";
import {View,Text,StyleSheet,TextInput,ActivityIndicator} from "react-native";
import colors from "../assets/colors";
import CustomActionButton from '../components/CustomActionButton'
import * as firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'
import axios from 'axios';
import { Notifications } from "expo";
const serverUrl = 'http://124.30.96.18:2000'
const http = axios.create({
    baseURL: serverUrl, 
});
class LoginScreen extends React.Component {
    constructor(){
        super()
        this.state={
            email:'',
            password:'',
            isLoading:false
        }
    }

    onSignIn = async () => {
      if(this.state.email && this.state.password)
      {
          this.setState({isLoading:true})
          try{
              const response = await firebase.auth()
              .signInWithEmailAndPassword(this.state.email,this.state.password)
            if(response)
            {
                this.setState({isLoading:false});
                var email = this.state.email
                http.post('/login',{email}).then((response) =>{
                if(response.data == "login success")
                {    
                this.props.navigation.navigate('LoadingScreen');
                console.log('moved to loading screen')
                }
                else{
                    alert("Please contact Dev/IT team")
                }
                })
                
            }
            }
          catch(error)
          {
            this.setState({isLoading:false})
            console.log(error.code)
             switch(error.code)
             {
                 case 'auth/user-not-found':
                     alert('User does not exist.Try signing up')
                     break;
                 case 'auth/invalid-email':
                     alert('Please enter an email address') 
                     break;
                 case 'auth/wrong-password':
                     alert('wrong password')       
             } 
          }
      }
      else{
          alert('Please enter email and password')
      }
    }
    onSignUp = async () => {
     if(this.state.email && this.state.password)
     {
        this.setState({isLoading:true})
         try{
           const response = await firebase.auth().createUserWithEmailAndPassword(this.state.email,this.state.password);
           const {email,password} = this.state 
         if(response)
         {
            this.setState({isLoading:false});
            http.post('/userinfo',{email,password}) 
            http.post('/login',{email})
            const userresponse = await firebase.auth().signInWithEmailAndPassword(this.state.email,this.state.password)
            this.props.navigation.navigate('SignInfo');
         }
        } 
         catch(error) {
            this.setState({isLoading:false});
             if(error.code == 'auth/email-already-in-use') 
             {
                 alert('User already Exists.Try Logging in');
             }
         }
     } else {
         alert('Please enter email and password');
     }
    };
    render() {
        return (
            <View style={styles.container}>
                {this.state.isLoading ? 
                    <View style={[StyleSheet.absoluteFill,{
                        alignItems:'center',
                        justifyContent:'center',
                        zIndex:1000,
                        elevation:1000
                    }]}>
                    <ActivityIndicator size="large" color={colors.logocolor}/>
                        </View>
                :null }
                <View style={{flex:1,justifyContent:'center',paddingTop:50}}>
              <TextInput style={styles.textInput}
              placeholder="Enter your Email Id"
              placeholderTextColor={colors.bgTextInputDark}
              keyboardType="email-address"
              onChangeText = {email => this.setState({email:email})}
              ></TextInput>  
              <TextInput style={styles.textInput}
              placeholder="Enter Password(min 10 characters)"
              placeholderTextColor={colors.bgTextInputDark}
              secureTextEntry
              onChangeText = {password => this.setState({password:password})}
              ></TextInput> 
              <View style={{flex:1,alignItems:'center'}}>
              <CustomActionButton onPress = {this.onSignIn} style={[styles.loginButtons,{ borderColor:colors.bgPrimary}]}>
               <Text style={{color:'white',fontWeight:'100'}}>Login</Text>
               </CustomActionButton>
               
           {/* <CustomActionButton onPress = {this.onSignUp} style={[styles.loginButtons,{ borderColor:colors.bgPrimary}]}>
               <Text style={{color:'white',fontWeight:'100'}}>Sign Up</Text>
                </CustomActionButton>*/}
              </View>
              </View>
              <View style={{flex:1}}>

             </View>
            </View>
          
        );
    }
}
export default LoginScreen;
const styles = StyleSheet.create({
    container: {
        flex:1,
        backgroundColor: colors.bgMain
    },
    textInput:{
        height:50,
        borderWidth:0.5,
        borderColor:colors.borderColor,
        marginHorizontal: 40,
        marginBottom: 10,
        color:colors.txtWhite,
        paddingHorizontal: 10
    },
    loginButtons:{
        borderWidth:0.5,
        backgroundColor:'transparent',
        marginTop:10,
        width:200
    }
});