import React, { Component } from 'react';
import {
  Platform,
  StyleSheet,
  ListView,
  TouchableOpacity,
  View,
  Image,
  Text,
  Alert,
  TouchableHighlight,
  TextInput,
  Picker
} from 'react-native';
import CustomActionButton from '../components/CustomActionButton'
import {Ionicons} from '@expo/vector-icons'
import colors from '../assets/colors';
import axios from 'axios';
import * as firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'
import * as ImagePicker from 'expo-image-picker';
import ModalSelector from 'react-native-modal-selector'
import { onChange } from 'react-native-reanimated';
const serverUrl = 'http://124.30.96.18:2000'
const http = axios.create({
    baseURL: serverUrl, 
});
export default class UserProfile extends React.Component {  
    constructor(){
        super()
        this.state={
            username:'',
            password:'',
            deptIndex:0,
            desigIndex:0,
            userRole:0,
            isLoading:false,
        }
    }

       takeAndUploadPhotoAsync = async () => {
         try{
        // Display the camera to the user and wait for them to take a photo or to cancel
        // the action
        let result = await ImagePicker.launchImageLibraryAsync({
          allowsEditing: true,
          aspect: [6, 6],
          quality: 1,
        });
      
        if (result.cancelled) {
          return;
        }
      
        // ImagePicker saves the taken photo to disk and returns a local URI to it
        let localUri = result.uri;
        let filename = localUri.split('/').pop();
        console.log(result.uri)
        // Infer the type of the image
        let match = /\.(\w+)$/.exec(filename);
        let type = match ? `image/${match[1]}` : `image`;
      
        // Upload the image using the fetch and FormData APIs
        let formData = new FormData();
        // Assume "photo" is the name of the form field the server expects
        formData.append('photo', { uri: localUri, name: filename, type });
        alert('profile picture uploaded successfully')
        this.props.navigation.navigate('HomeScreen')
      
        return await fetch('http://124.30.96.18:2000/uploadphoto', {
          method: 'POST',
          body: formData,
          headers: {
            'content-type': 'multipart/form-data',
          },
        });
      }
      catch(error)
      {
        alert('picture update unsuccessful')
      }
      }

    goback = () => {
      this.props.navigation.navigate('HomeScreen')
    }


    createUser = () => {
      try{
          if(this.state.username && this.state.desigIndex!=0 && this.state.deptIndex!=0 && this.state.userRole!=0)
              {
                  try{
                    const username = this.state.username 
                    var desig = this.state.desigIndex
                    var dept = this.state.deptIndex
                    var role = this.state.userRole
                    http.post('/profile',{username,desig,dept,role}) 
                    alert('Profile Updated Successfully')
                    this.props.navigation.navigate('HomeScreen')
                 } 
                  catch(error) {
                     alert('Profile Update Unsuccessful')
                  }
              } 
              else {
                  alert('Please enter all the details');
              }
      }
      catch(error)
      {
        alert('Profile update unsuccessful')
      }
    }
  render() {
        const department = [
            { key: 0, section: true, label: 'Department' },
            { key: 1, label: 'Information Technology' },
            { key: 2, label: 'IT Development' },
            { key: 3, label: 'Front Office'},
            { key: 4, label: 'Human Resource'},
            { key: 5, label: 'Accounts'},
            { key: 6, label: 'Marketing'},
            { key: 7, label: 'Corporate'},
            { key: 8, label: 'Management'},
            { key: 9, label: 'Tele Calling'},
        ];
        const designation = [
          { key: 0, section: true, label: 'Designation' },
          { key: 1, label: 'Managing Director'},
          { key: 2, label: 'Joint Managing Director' },
          { key: 3, label: 'General Manager'},
          { key: 4, label: 'Manager'},
          { key: 5, label: 'Operation Manager'},
          { key: 6, label: 'Assistant Operation Manager'},
          { key: 7, label: 'Sales Manager'},
          { key: 8, label: 'Developer'},
          { key: 9, label: 'Employee'},
          {key: 10, label: 'Doctor'}
      ];
      const role = [
        { key: 0, section: true, label: 'Role' },
        { key: 1, label: 'Admin - MD'},
        { key: 2, label: 'Manager' },
        { key: 3, label: 'Employee'},
    ];
    return (
      <View style={styles.container}>
          <CustomActionButton onPress = {this.takeAndUploadPhotoAsync} style={styles.icon}>
           <Ionicons name="ios-contact" size={100}/>
          </CustomActionButton> 
          <View style={{flex:1,alignItems:'center',paddingTop:50}}>
          <Text style={{ height: 50, width: 250,color:colors.txtWhite }}>Click icon to choose profile picture</Text>
          <TextInput style={styles.textInput}
              placeholder="Enter your Username"
              placeholderTextColor={colors.bgTextInputDark}
              keyboardType="email-address"
              onChangeText = {username => this.setState({username:username})} >
          </TextInput> 
          <ModalSelector style={{height: 50, width:250}}
                    data={department}
                    initValue="Choose your department"
                    onChange={(option)=>{}}
                    onChange= {(option)=>{ this.setState({deptIndex:option.key})
                    alert(option.label)
                  }}
                    
                    />
          <ModalSelector style={{height: 50, width:250}}
                    data={designation}
                    initValue="Choose your designation"
                    accessible={true} 
                    onChange= {(option)=>{ this.setState({desigIndex:option.key})
                    alert(option.label)
                  }}                
                    />
           <ModalSelector style={{height: 50, width:250}}
                    data={role}
                    initValue="Choose your role"
                    onChange={(option)=>{}}
                    onChange= {(option)=>{ this.setState({userRole:option.key})
                    alert(option.label)
                  }}
                    />                           
      <CustomActionButton onPress = {this.createUser} style={[styles.loginButtons,{ borderColor:colors.bgPrimary}]}>
       <Text style={{color:'white',fontWeight:'100'}}>Update Profile</Text>
      </CustomActionButton> 
      <CustomActionButton
        position="right" 
        style={{ backgroundColor: colors.bgPrimary,borderRadius:25 }}
        onPress={this.goback}>
       <Ionicons style={styles.iconnew} name="ios-undo" size={24}/>
        </CustomActionButton> 
      </View>
      </View>
    )
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#EEEEEE',
    alignItems: 'center',
    paddingTop:50,
    backgroundColor:colors.bgMain
  },
  icon:{
    width:100,
    height:100,
    borderRadius:100
  },
  iconnew:{
    width:30,
    height:30,
  },
  title:{
    fontSize:24,
    textAlign: 'center',
    marginTop:22,
    color: "#5F6D7A"
  },
  description: {
    marginTop:20,
    textAlign: 'center',
    color: "#A9A9A9",
    fontSize:16,
    margin:40,
  },
  buttonContainer: {
    height:45,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    marginBottom:20,
    width:250,
    borderRadius:30,
  },
  loginButton: {
    backgroundColor: "#3498db",
  },
  buttonText: {
    color: "#FFFFFF",
    fontSize:20,
  },
  textInput:{
    height:50,
    width:250,
    borderWidth:1,
    borderColor:colors.borderColor,
    marginHorizontal: 40,
    marginBottom: 10,
    color:colors.txtWhite,
    paddingHorizontal: 10
},
loginButtons:{
    borderWidth:0.5,
    backgroundColor:'transparent',
    marginTop:10,
    width:250
},
});