import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  TextInput,
  Button,
  TouchableHighlight,
  Alert,
  Image,
  ListView,
  TouchableOpacity,
  FlatList,
  ScrollView,
  Modal,
  ActivityIndicator
} from 'react-native';
import {NavigationEvents} from 'react-navigation'
import colors from '../assets/colors';
import Ionicons from '@expo/vector-icons/Ionicons'
import axios from 'axios'
import CustomActionButton from '../components/CustomActionButton'
import { ConfirmDialog } from 'react-native-simple-dialogs'
const serverUrl = 'http://124.30.96.18:2000'
const http = axios.create({
    baseURL: serverUrl, 
});



export default class SpecificUser extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      data:[],
      modalVisible:false,
      userSelected:[],
      repeatitem:[],
      finishitem:[],
      reassignVisible:false,
      finishVisible:false,
      isLoading:false,
      assignedby:"",

    };
    this.repeat = this.repeat.bind(this);
    this.close = this.close.bind(this);
  }
  componentDidMount(){
    try{
      this.setState({isLoading: true});
      var userid = this.props.navigation.getParam('username');
      http.post('/specificuser',{userid})
      .then((response) =>{
        if(response.data)
        {
            axios.get("http://124.30.96.18:2000/specificuser")
            .then((response) =>{
            this.setState({data: response.data});
            console.log(this.state.data)
            this.setState({isLoading: false});
          })
        }
        else{
       alert(response.data)
        }
    })
  }
  catch(error)
  {
    alert('Connection to server lost')
  }
  }
  clickEventListener = (item) => {
    this.setState({userSelected: item}, () =>{
      var fuser = item.fromuser
      http.post('/fromuser',{fuser}).then((response) =>{
        if(response.data)
        {
          this.setState({assignedby: response.data})
          console.log(response.data)
          this.setState({modalVisible: true});
        }
      })
    });
  }
  setModalVisible(visible) {
    this.setState({modalVisible: visible});
  }
  showAddNewTask = () => {
    this.props.navigation.navigate('AssignedTask');
  };
  repeat = (item) => {
    this.setState({reassignVisible: true})
    this.setState({repeatitem: item})
  }
  close = (item) => {
    this.setState({finishVisible: true})
    this.setState({finishitem: item})
  }
  goback = () => {
    this.props.navigation.navigate('Userwisetask')
  }
  reassign = () => {
    try{
    var taskid = this.state.repeatitem.taskid
    http.post('/repeat',{taskid})
    .then(() =>{
      axios.get("http://124.30.96.18:2000/specificuser")
      .then((response) =>{
        this.setState({data: response.data});
        console.log(this.state.data)
      })
    })
    alert('Task reassigned successfully')
    this.setState({reassignVisible: false})
    }
    catch(error)
    {
      alert('Connection to server lost')
    }
  };

  finish = () => {
    try{
    var taskid = this.state.finishitem.taskid
    http.post("/close",{taskid})
    .then(() =>{
      axios.get("http://124.30.96.18:2000/specificuser")
      .then((response) =>{
        this.setState({data: response.data});
        console.log(this.state.data)
      })
    })
    alert('Task closed successfully')
    this.setState({finishVisible: false})
    }
    catch(error)
    {
      alert('Connection to server lost')
    }
  }

  render() {
    return (
      <View style={{ flex: 1 }} >
          <Text></Text>
          <Text></Text>
      <FlatList 
        data={this.state.data}
        keyExtractor= {(item) => {
            return item.taskid.toString();
          }}
          ListEmptyComponent={
            <View style = {{ marginTop:50, alignItems: 'center' }}>
              <Text style={{ fontWeight:'bold'}}>
                No jobs assigned
              </Text>
              </View>
          }
        renderItem={({item}) => {
          return (
            <TouchableOpacity onPress={() => {this.clickEventListener(item)}}>
            <View style={styles.box}>
              <Image style={styles.image} source={{uri: item.image}} />
              <View style={styles.boxContent}>
                <Text style={styles.title}>{item.taskname}</Text>
                <Text style={styles.name}>{item.touser}</Text>
                {item.status=='Incomplete'?<Text style={styles.description}>{item.status}</Text>:<Text style={styles.completedescription}>{item.status}</Text>}
                <View style={styles.buttons}>
                </View>         
              </View>
                </View>      
            </TouchableOpacity>      
          )
        }}/>
        {this.state.isLoading?
        <View style={styles.container}>
        <ActivityIndicator size="large" color={colors.logoColor}></ActivityIndicator>
        </View>
        :null}   
        <ConfirmDialog
    title="Please Confirm"
    message="Do you want to remove the task?"
    visible={this.state.finishVisible}
    onTouchOutside={() => this.setState({finishVisible: false})}
    positiveButton={{
        title: "YES",
        onPress: () => this.finish()
    }}
    negativeButton={{
        title: "NO",
        onPress: () => this.setState({finishVisible: false})
    }}
       />
       <ConfirmDialog
    title="Please Confirm"
    message="Do you want to reassign the task?"
    visible={this.state.reassignVisible}
    onTouchOutside={() => this.setState({reassignVisible: false})}
    positiveButton={{
        title: "YES",
        onPress: () => this.reassign()
    }}
    negativeButton={{
        title: "NO",
        onPress: () => this.setState({reassignVisible: false})
    }}
       />
        <Modal
        animationType={'fade'}
        transparent={true}
        onRequestClose={() => this.setModalVisible(false)}
        visible={this.state.modalVisible}>

        <View style={styles.popupOverlay}>
          <View style={styles.popup}>
            <View style={styles.popupContent}>
              <ScrollView contentContainerStyle={styles.modalInfo}>
                  <Image style={styles.imagepop} source={{uri: this.state.userSelected.image}}/>
                  <Text style={styles.namepop}>{this.state.userSelected.touser}</Text>
                  <Text style={styles.position}>{this.state.userSelected.designation}</Text>
                  <Text style={styles.abouttext}>Assigned by: {this.state.assignedby}</Text>
                  {this.state.userSelected.completetime?<Text style={styles.abouttext}>Last Date:{this.state.userSelected.completetime}</Text>:<Text></Text>}
                  <Text style={styles.about}>{this.state.userSelected.taskname}</Text>
                  <Text style={styles.abouttext}>{this.state.userSelected.taskinfo}</Text>
              </ScrollView>
            </View>
            <View style={styles.popupButtons}>
              <TouchableOpacity onPress={() => {this.setModalVisible(false) }} style={styles.btnClose}>
                <Text style={styles.txtClose}>Close</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </Modal>
      <CustomActionButton
        position="right" 
        style={{ backgroundColor: colors.bgPrimary,borderRadius:25 }}
        onPress={this.goback}>
       <Ionicons style={styles.icon} name="ios-undo" size={24}/>
        </CustomActionButton>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: "center"
  },
  image: {
    width: 70,
    height:70,
  },
  box: { 
    flex: 1,
    padding:0,
    marginTop:5,
    marginBottom:5,
    backgroundColor: 'white',
    flexDirection: 'row',
    borderTopColor: colors.bgMain,
  },
  boxContent: {
    flex:1,
    flexDirection: 'column',
    alignItems: 'flex-start',
    marginLeft:10,
  },
  buttonContent: {
    flex:1,
    flexDirection: 'row',
    alignItems: 'flex-start',
    marginLeft:10,
  },
  title:{
    fontSize:18,
    color:"#151515",
  },
  name:{
    fontSize:16,
    color:"#151515",
  },
  description:{
    fontSize:15,
    color: "#8B0000",
  },
  completedescription:{
    fontSize:15,
    color: "#228B22",
  },
  buttons:{
    flexDirection: 'row',
  },
  button: {
    height:35,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius:10,
    width:50,
    marginRight:5,
    marginTop:5,
  },
  icon:{
    width:20,
    height:20,
  },
  view: {
    backgroundColor: "#FF1493",
  },
  profile: {
    backgroundColor: "#1E90FF",
  },
  message: {
    backgroundColor: "#228B22",
  },
   /************ modals ************/
   popup: {
    backgroundColor: 'white',
    marginTop: 80,
    marginHorizontal: 20,
    borderRadius: 7,
  },
  popupOverlay: {
    backgroundColor: "#00000057",
    flex: 1,
    marginTop: 30
  },
  popupContent: {
    //alignItems: 'center',
    margin: 5,
    height:250,
  },
  popupHeader: {
    marginBottom: 45
  },
  popupButtons: {
    marginTop: 15,
    flexDirection: 'row',
    borderTopWidth: 1,
    borderColor: "#eee",
    justifyContent:'center'
  },
  popupButton: {
    flex: 1,
    marginVertical: 16
  },
  btnClose:{
    height:20,
    backgroundColor:'#20b2aa',
    padding:20
  },
  modalInfo:{
    alignItems:'center',
    justifyContent:'center',
  },
  namepop:{
    fontSize:18,
    flex:1,
    alignSelf:'center',
    color:"#008080",
    fontWeight:'bold'
  },
  position:{
    fontSize:16,
    flex:1,
    alignSelf:'center',
    color:"#008080"
  },
  about:{
    marginHorizontal:10,
    color:"#FF0000"
  },
  abouttext:{
    marginHorizontal:10,
    color:"#000000"
  },
  imagepop:{
    width:90,
    height:90,
    borderRadius:45,
  },
});