import React from 'react';
import {
  StyleSheet,
  Text,
  View,
  TextInput,
  TouchableHighlight,
  Image,
  TouchableOpacity,
  FlatList,
  ActivityIndicator
} from 'react-native';
import {NavigationEvents} from 'react-navigation'
import colors from '../assets/colors';
import Ionicons from '@expo/vector-icons/Ionicons'
import axios from 'axios'
import CustomActionButton from '../components/CustomActionButton'
import { ConfirmDialog } from 'react-native-simple-dialogs'
const serverUrl = 'http://124.30.96.18:2000'
const http = axios.create({
    baseURL: serverUrl,
});



export default class Mylist extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      data:[],
      finishVisible:false,
      isLoading:false,
      taskname:"",
      finishitem:""
    };
  }
  componentDidMount(){
    try{
      this.setState({isLoading: true});
      axios.get("http://124.30.96.18:2000/todotasks")
      .then((response) =>{
      this.setState({data: response.data});
      console.log(this.state.data)
      this.setState({isLoading: false});
    })
  }
  catch(error)
  {
    alert('Connection to server lost')
  }
  }
  

  close = (item) => {
    this.setState({finishVisible: true})
    this.setState({finishitem: item})
  }
  goback = () => {
    this.props.navigation.navigate('HomeScreen')
  }

  finish = () => {
    try{
    var taskid = this.state.finishitem.taskid
    http.post("/finishtodo",{taskid})
    .then(() =>{
      axios.get("http://124.30.96.18:2000/todotasks")
      .then((response) =>{
        this.setState({data: response.data});
        console.log(this.state.data)
      })
    })
    alert('Task closed successfully')
    this.setState({finishVisible: false})
    }
    catch(error)
    {
      alert('Connection to server lost')
    }
  }

  addTodo = () => {
      try{
        var task = this.state.taskname
        http.post("/addtodo",{task})
        .then((response) => {
            axios.get("http://124.30.96.18:2000/todotasks")
            .then((response) =>{
                this.setState({data: response.data});
                console.log(this.state.data)
                alert('Task added successfully')
              })
        }
        )
      }
      catch(error)
      {
        alert(error)
      }
  }
  render() {
    return (
      <View style={{ flex: 1,marginTop:50,marginBottom:50}} >
             <NavigationEvents onDidFocus={()=>
            axios.get("http://124.30.96.18:2000/todotasks")
            .then((response) =>{
              this.setState({data: response.data});
              console.log(this.state.data)
            })
          }/>
              <View style={{height:50,flexDirection:"row"}}>
                  <TextInput
                  onChangeText={(text)=>this.setState({taskname:text})}
                  style={{flex:1,backgroundColor: '#ececec',paddingLeft:10}}
                  placeholder='Enter task to add in the list'
                  placeholderTextColor="grey"
                  />
                  <TouchableOpacity onPress={this.addTodo}>
                  <View style={{height:50,width: 50,backgroundColor:"#a5deba",alignItems:'center',justifyContent:'center'}}>
                      <Ionicons name='ios-checkmark' color='white' size={40}></Ionicons>
                  </View>
                  </TouchableOpacity>
          </View>
      <FlatList
        data={this.state.data}
        keyExtractor= {(item) => {
            return item.taskid.toString();
          }}
          ListEmptyComponent={
            <View style = {{ marginTop:50, alignItems: 'center' }}>
              <Text style={{ fontWeight:'bold'}}>
                To Do List is Empty
              </Text>
              </View>
          }
        renderItem={({item}) => {
          return (
            <View style={styles.box}>
              <View style={styles.boxContent}>
                <Text style={styles.title}>{item.taskname}</Text>
              </View>
                  <TouchableHighlight style={[styles.button, styles.profile]} onPress={() => this.close(item)}>
                  <Ionicons style={styles.icon} name="ios-checkmark-circle-outline" size={22}/>
                  </TouchableHighlight>
                </View>
          )
        }}/>
        {this.state.isLoading?
        <View style={styles.container}>
        <ActivityIndicator size="large" color={colors.logoColor}></ActivityIndicator>
        </View>
        :null}
        <ConfirmDialog
    title="Please Confirm"
    message="Do you want to complete the task?"
    visible={this.state.finishVisible}
    onTouchOutside={() => this.setState({finishVisible: false})}
    positiveButton={{
        title: "YES",
        onPress: () => this.finish()
    }}
    negativeButton={{
        title: "NO",
        onPress: () => this.setState({finishVisible: false})
    }}
       />
        <CustomActionButton
        position="right"
        style={{ backgroundColor: colors.bgPrimary,borderRadius:25 }}
        onPress={this.goback}>
       <Ionicons style={styles.icon} name="ios-undo" size={24}/>
        </CustomActionButton>
      </View>
    );
  }
}

