import React from 'react';
import {
    StyleSheet,
    Text,
    View,
    TextInput,
    TouchableOpacity,
    Alert,
    BackHandler,
} from 'react-native';
import colors from '../assets/colors';
import axios from 'axios';
import DateTimePickerModal from 'react-native-modal-datetime-picker';
import {Notifications} from 'expo';
import * as Permissions from 'expo-permissions'
import firebase from 'firebase';
const serverUrl = 'http://124.30.96.18:2000'
const http = axios.create({
    baseURL: serverUrl, 
});

export default class TaskPage extends React.Component {
    constructor(){
        super()
        this.state={
            taskname:'',
            taskinfo:'',
            user:'',
            visibility:false,
            DateDisplay:"",
        }
    }


    backAction = () => {
        Alert.alert("Hold on!", "Are you sure you want to go back?", [
          {
            text: "Cancel",
            onPress: () => null,
            style: "cancel"
          },
          { text: "YES", onPress: () => this.goback() }
        ]);
        return true;
      };
    
       async componentDidMount() {
        this.currentUser = await firebase.auth().currentUser
        BackHandler.addEventListener("hardwareBackPress", this.backAction);
      }
    
      componentWillUnmount() {
        BackHandler.removeEventListener("hardwareBackPress", this.backAction);
      }
     
    
    goback = () => {
        this.props.navigation.navigate('AssignedTask')
      }
    assign = () => {
        try{
        if(this.state.taskname)
        {
        var usr = this.props.navigation.getParam('username');   
        var taskname = this.state.taskname
        var taskinfo = this.state.taskinfo
        var date = this.state.DateDisplay
        http.post('/assigntask',{usr,taskname,taskinfo,date})
        alert('Task assigned successfully')
        http.post('/gettoken',{usr}).then((response) =>{
          if(response.data)
          {
          try{
          let notification = fetch('https://exp.host/--/api/v2/push/send',{
          method:'POST',
          headers:{
            Accept:'application/json',
            'Content-Type':'application/json'
          },
          body:JSON.stringify({
            to: response.data,
            sound: 'default',
            title: 'Wee Do',
            body: taskname
          })
          });
        }catch(error)
        {
          alert(error)
        }
        this.props.navigation.navigate('AssignedJobs')
          }
          else{
            this.props.navigation.navigate('AssignedJobs')
          }
      })
        }
        else
        {
          alert('Please enter taskname')  
        }
    }
      catch(error)
      {
         alert(error) 
      }
        }  

        handleConfirm = (date) => {
          this.setState({DateDisplay:date.toUTCString()})
          console.log(this.state.DateDisplay)
        }
    
        onPressCancel=()=>{
          this.setState({visibility:false})
        }
    
        onPressButton=()=>{
          this.setState({visibility:true})
        }

        registerForPushNotificationAsync = async() => {
          try{
          let token = await Notifications.getExpoPushTokenAsync();
          console.log(token)
        }
        catch(error)
        {
          console.log(error)
        }
        }  
            
  
    render() {
        return (
            <View style={styles.container}>
            <View style={styles.regform}>
            <Text style={styles.header}>Task Details</Text>
            <TextInput style={styles.textInput} placeholder="Task Name"
             placeholderTextColor={colors.bgTextInputDark}
             onChangeText = {taskname => this.setState({taskname:taskname})} />
            <TextInput style={styles.textInput} placeholder="Task Info"  
            placeholderTextColor={colors.bgTextInputDark}
            onChangeText = {taskinfo => this.setState({taskinfo:taskinfo})} />
            <Text style={styles.text}>{this.state.DateDisplay}</Text>
            <TouchableOpacity style={styles.datepicker} onPress = {this.onPressButton}>
                <Text style={styles.datetext}>Select Date To Complete</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button} onPress = {this.assign}>
                <Text style={styles.btntext}>Assign</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button} onPress = {this.goback}>
                <Text style={styles.btntext}>Go Back</Text>
            </TouchableOpacity>
            <DateTimePickerModal
            isVisible={this.state.visibility}
            onConfirm={this.handleConfirm}
            onCancel={this.onPressCancel}
            mode="date"
            />
            </View>
            </View>
        );
    }
}

const styles = StyleSheet.create({
container:{
  flex: 1,
  justifyContent:'center',
  backgroundColor: '#36485f',
  paddingLeft: 60,
  paddingRight: 60,
},    
regform: {
    alignSelf:'stretch',
},
header: {
    fontSize: 24,
    color: '#fff',
    paddingTop: 50,
    paddingBottom: 10,
    marginBottom: 40,
    borderBottomColor: '#199187',
    borderBottomWidth: 1,
},
textInput: {
    alignSelf:'stretch',
    height:40,
    marginBottom: 30,
    color: '#FFFFFF',
    borderBottomColor: '#f8f8f8',
    borderBottomWidth: 1,
},
text: {
  alignSelf:'stretch',
  color: '#fff',
},
button: {
    alignSelf: 'stretch',
    alignItems: 'center',
    padding: 20,
    backgroundColor: '#59cbbd',
    marginTop: 30
},
datepicker: {
  alignItems: 'center',
  padding: 15,
  backgroundColor: '#00BFFF',
  marginTop: 30
},
btntext: {
    color: '#fff',
    fontWeight: 'bold',
},
datetext: {
   color: '#000',
   fontWeight: 'bold',
},
});
