import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  Image,
  TouchableOpacity,
} from 'react-native';
import axios from 'axios'
import colors from '../assets/colors';


export default class Profile extends React.Component {

    constructor(props){
        super(props)
        this.state={
            data:[],
        }
    }

    componentDidMount(){
        try{
        axios.get("http://124.30.96.18:2000/myprofile")
        .then((response) =>{
          this.setState({data: response.data});
          console.log(this.state.data)
        })
      }
      catch(error)
      {
        alert('Connection to server lost')
      }
      }

    

    goback = () => {
        this.props.navigation.navigate('HomeScreen')
      }

  render() {
    return this.state.data.map((data,i) => {
    return (
      <View style={styles.container}>
          <View style={styles.header}>
            <View style={styles.headerContent}>
                <Image key={data.image} style={styles.avatar} source={{uri: data.image}}/>
                <Text style={styles.name}>
                {data.username}
                </Text>
            </View>
          </View>

          <View style={styles.profileDetail}>
            <View style={styles.detailContent}>
              <Text style={styles.title}>Tasks</Text>
              <Text key={data.image} style={styles.count}>{data.taskcount}</Text>
            </View>
            <View style={styles.detailContent}>
              <Text style={styles.title}>Complete</Text>
              <Text key={data.image} style={styles.count}>{data.complete}</Text>
            </View>
            <View style={styles.detailContent}>
              <Text style={styles.title}>Incomplete</Text>
              <Text key={data.image} style={styles.count}>{data.incomplete}</Text>
            </View>
          </View>

          <View style={styles.body}>
            <View style={styles.bodyContent}>
              <Text style={styles.description}>Working at Aarthi scans and Labs</Text>
              <Text style={styles.description}></Text>
              <Text style={styles.description}></Text>
              <Text style={styles.description}></Text>

              <TouchableOpacity style={styles.buttonContainer} onPress = {this.goback}>
                <Text style={styles.btntext}>Home</Text>  
              </TouchableOpacity> 
            </View>
        </View>
      </View>
    );
  });
  }
}

const styles = StyleSheet.create({
  header:{
    backgroundColor: colors.bgMain,
  },
  headerContent:{
    padding:30,
    alignItems: 'center',
  },
  avatar: {
    width: 130,
    height: 130,
    borderRadius: 63,
    borderWidth: 4,
    borderColor: "white",
    marginBottom:10,
  },
  name:{
    fontSize:22,
    color:"#FFFFFF",
    fontWeight:'600',
  },
  profileDetail:{
    alignSelf: 'center',
    marginTop:200,
    alignItems: 'center',
    flexDirection: 'row',
    position:'absolute',
    backgroundColor: "#ffffff"
  },
  detailContent:{
    margin:10,
    alignItems: 'center'
  },
  title:{
    fontSize:20,
    color: colors.bgMain
  },
  count:{
    fontSize:18,
  },
  bodyContent: {
    flex: 1,
    alignItems: 'center',
    padding:30,
    marginTop:40
  },
  textInfo:{
    fontSize:18,
    marginTop:20,
    color: "#696969",
  },
  buttonContainer: {
    marginTop:10,
    height:45,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    marginBottom:20,
    width:250,
    borderRadius:30,
    backgroundColor: colors.bgMain,
  },
  description:{
    fontSize:20,
    color: colors.bgMain,
    marginTop:10,
    textAlign: 'center'
  },
  btntext:{
    color:"#FFFFFF"
  }
});
 